// Expose a way to enumerate all possible values for the union type.
export const ALL_PILL_VARIANTS = [
  'emphasis',
  'success',
  'warning',
  'critical',
  'normal',
  'insight',
  'alpha',
  'beta',
] as const;

// prettier wants (typeof ALL_PILL_VARIANTS)[number] but that change seems wrong
// prettier-ignore
export type MarketPillVariant = typeof ALL_PILL_VARIANTS[number]; // TODO: prefix types with `T`
